body {
  background-color: whitesmoke;
  font-family: "Open Sans", Helvetica, Arial, sans-serif; }

.display {
  align-items: center;
  background-color: #282C34;
  border: 1rem solid #32363E;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  height: 14rem;
  justify-content: center;
  margin: 1rem auto;
  width: 14rem; }

.display-date {
  color: #61DAFB;
  font-size: 14px;
  font-weight: 300; }

.display-time {
  color: #61DAFB;
  font-size: 42px;
  font-weight: 600; }

.panel {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 4rem;
  justify-content: center;
  margin: 1rem auto;
  width: 14rem; }

.panel-switch-text {
  font-size: 2rem;
  padding: 0.5rem;
  color: #333; }

/**
 * The following block of CSS is taken directly from https://www.w3schools.com/howto/howto_css_switch.asp
 */
/* The switch - the box around the slider */
.switch {
  padding: 0.5rem;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px; }

/* Hide default HTML checkbox */
.switch input {
  display: none; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s; }

input:checked + .slider {
  background-color: #61DAFB; }

input:focus + .slider {
  box-shadow: 0 0 1px #61DAFB; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

/* Rounded sliders */
.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }

.header {
  height: 100px; }
